body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

a{
    text-decoration: none;
    color: white;
}

nav{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 10px 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #1F2633;
}
nav > section {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}
.rightSideNav{
    grid-gap: 30px;
    gap: 30px;
}
.leftSideNav{
    grid-gap:10px;
    gap:10px;
}
section > *{
    color: white;
}

.dark-light-mode-checkbox{
    width:0;
    height: 0;
    opacity: 0;
}
.dark-light-mode-toggle-label{
    width:51px;
    height: 31px;
    background-color: #c5d1dc84;
    border-radius: 30px;  
    position: relative;  
    transition: background-color 200ms ease-in;
}
.dark-light-mode-enabled-circle{
    width:27px;
    height: 27px;
    background-color: #ffffff;
    border-radius: 50%;
    margin: 2px;
    transition: -webkit-transform 200ms ease-in;
    transition: transform 200ms ease-in;
    transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
    /* background: linear-gradient(to left, ); */
}
.currentPage{
    text-decoration: underline;
    color: white;
}
.notCurrentPage{
    text-decoration: none;
}

button{
    text-decoration: none;
    border: none;
}

.container{
    margin: 50px auto;
    width:70%;
    height:50vh;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    grid-gap: 10px;
    gap: 10px;
  /* background-color: rgb(185, 192, 198); */

}
.container > strong{
    font-size: 30px;
    color:coral;
}
#text-area{
    width:80%;
    height: 100vh;
    margin-top: 10px;
}
.buttons{
    display: flex;
    flex-direction: row;
    grid-gap:50px;
    gap:50px;
}
.buttons > button{
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    padding: 10px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    color: white;
    background-color: green;
}
.contact-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap:20px;
    gap:20px;
    width:70%;
    margin: 0 auto;
    /* padding: 10px 20px; */
}
.contact-container > h1{
    color: coral;
}
.contact-container > p{
    text-align: center;
}
.contact-details{
    display: flex;
    flex-direction: column;
    grid-gap:20px;
    gap:20px;
    justify-content: center;
    align-items: center;
}
.contact-details > div{
    display: flex;
    flex-direction: row;
    grid-gap:10px;
    gap:10px;
}
.contact-details input{
    width:16vw;
    height:4vh;
}
.contact-details textarea{
    width:35vw;
    height: 30vh;
}

